.cssload-loading {
	position: absolute;
	left: 50%;
	margin: -34px 0 0 -55px;
	width: 117px;
	height: 68px;
	*zoom: 1;
}
.cssload-loading:before,
.cssload-loading:after {
	display: table;
	content: "";
}
.cssload-loading:after {
	clear: both;
}
.cssload-loading .cssload-finger {
	float: left;
	margin: 0 2px 0 0;
	width: 19px;
	height: 100%;
}
